
.search {
  position: fixed;
  top: 0;
  height: 100%;
  width: 100%;
  background: #f3f3f5;
}
.search .search-box-wrapper {
  height: 50px;
  background: #d90b01;
}
.search .shortcut-wrapper {
  position: fixed;
  top: 75px;
  bottom: 0;
  width: 100%;
}
.search .shortcut-wrapper .shortcut {
  height: 100%;
  overflow: hidden;
}
.search .shortcut-wrapper .shortcut .hot-key {
  margin: 0 20px 20px 20px;
}
.search .shortcut-wrapper .shortcut .hot-key .title {
  margin-bottom: 20px;
  font-size: 14px;
  color: rgba(53,52,52,0.5);
}
.search .shortcut-wrapper .shortcut .hot-key .item {
  display: inline-block;
  padding: 5px 10px;
  margin: 0 20px 10px 0;
  border-radius: 6px;
  background: #fff;
  font-size: 14px;
  color: rgba(0,0,0,0.6);
}
.search .shortcut-wrapper .shortcut .search-history {
  position: relative;
  margin: 0 20px;
}
.search .shortcut-wrapper .shortcut .search-history .search-list .search-item {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  height: 40px;
  overflow: hidden;
}
.search .shortcut-wrapper .shortcut .search-history .search-list .search-item.list-enter-active,
.search .shortcut-wrapper .shortcut .search-history .search-list .search-item.list-leave-active {
  -webkit-transition: all 0.1s;
  transition: all 0.1s;
}
.search .shortcut-wrapper .shortcut .search-history .search-list .search-item.list-enter,
.search .shortcut-wrapper .shortcut .search-history .search-list .search-item.list-leave-to {
  height: 0;
}
.search .shortcut-wrapper .shortcut .search-history .search-list .search-item .text {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  color: rgba(53,52,52,0.5);
}
.search .shortcut-wrapper .shortcut .search-history .search-list .search-item .icon {
  position: relative;
}
.search .shortcut-wrapper .shortcut .search-history .search-list .search-item .icon:before {
  content: '';
  position: absolute;
  top: -10px;
  left: -10px;
  right: -10px;
  bottom: -10px;
}
.search .shortcut-wrapper .shortcut .search-history .search-list .search-item .icon .icon-delete {
  font-size: 12px;
  color: rgba(0,0,0,0.6);
}
.search .shortcut-wrapper .shortcut .search-history .title {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  height: 40px;
  font-size: 14px;
  color: rgba(53,52,52,0.5);
}
.search .shortcut-wrapper .shortcut .search-history .title .text {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}
.search .shortcut-wrapper .shortcut .search-history .title .clear {
  position: relative;
}
.search .shortcut-wrapper .shortcut .search-history .title .clear:before {
  content: '';
  position: absolute;
  top: -10px;
  left: -10px;
  right: -10px;
  bottom: -10px;
}
.search .shortcut-wrapper .shortcut .search-history .title .clear .icon-clear {
  font-size: 14px;
  color: rgba(0,0,0,0.6);
}
.search .search-result {
  position: fixed;
  width: 100%;
  top: 65px;
  bottom: 0;
  z-index: -1;
}


